import React, { useState } from 'react';
import s from './servises.css';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import photo1 from '../../img/1_640.png';
import photo2 from '../../img/2_640.jpg';
import photo3 from '../../img/3_640.jpg';
import photo4 from '../../img/4_640.jpg';
import photo5 from '../../img/5_640.png';



  class Servises extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        isToggleOn: false
      };
      this.handleClick = this.handleClick.bind(this);
    }
  
    handleClick() {
      this.setState(state => ({
        isToggleOn: !state.isToggleOn
      }));
    }


    render() {
      return (
        
        <div 
          className = 'servises' 
          id={this.props.id} 
          style= {this.state.isToggleOn ? {height:'100%'}:{height: '100vh'}}
          style= {this.state.isToggleOn ? {margin: '30px 0 30px 0'}:{margin: '0' }}
        >
          <div className = 'servisesTitle'>
            <h2>Наши услуги</h2>
          </div>
          <ReactCSSTransitionGroup
              className = "wrapper"
              transitionName="example"
              transitionEnterTimeout={500}
              transitionLeaveTimeout={500}
          >
            
          <div className = 'servisesItem'>
            <div className = 'item'>
              <div className = 'itemFhoto'>
                <img src={photo1} alt=""/>
              </div>
              <div>Системы автоматического пожаротушения</div> 
            </div>
            <div className = 'item'>
            <div className = 'itemFhoto'>
                <img src={photo2} alt=""/>
              </div>
              <p>Системы автоматической пожарной сигнализации</p>
            </div>
            <div className = 'item'>
            <div className = 'itemFhoto'>
                <img src={photo3} alt=""/>
              </div>
              <p>Системы оповещения и управления эвакуацией людей при пожаре</p>
            </div>
            <div className = 'item'>
            <div className = 'itemFhoto'>
                <img src={photo4} alt=""/>
              </div>
              <p>Системы оповещения и управления эвакуацией людей при пожаре</p>
            </div>


            <div className = 'item'>
              <div className = 'itemFhoto'>
                <img src={photo1} alt=""/>
              </div>
              <div>Системы автоматического пожаротушения</div> 
            </div>
            <div className = 'item'>
            <div className = 'itemFhoto'>
                <img src={photo2} alt=""/>
              </div>
              <p>Системы автоматической пожарной сигнализации</p>
            </div>
            <div className = 'item'>
            <div className = 'itemFhoto'>
                <img src={photo3} alt=""/>
              </div>
              <p>Системы оповещения и управления эвакуацией людей при пожаре</p>
            </div>
            <div className = 'item'>
            <div className = 'itemFhoto'>
                <img src={photo4} alt=""/>
              </div>
              <p>Системы оповещения и управления эвакуацией людей при пожаре</p>
            </div>



          </div>
          <div className = 'buttonForMore'>
    
          
          <ReactCSSTransitionGroup
              
              transitionName="example"
              transitionEnterTimeout={500}
              transitionLeaveTimeout={500}
          >
            {/* HIDE or SHOW */}
              {this.state.isToggleOn 
            ? <div className = 'servisesItem'>
            <div className = 'item'>
              <div className = 'itemFhoto'>
                <img src={photo1} alt=""/>
              </div>
              <div>Системы автоматического пожаротушения</div> 
            </div>
            <div className = 'item'>
            <div className = 'itemFhoto'>
                <img src={photo2} alt=""/>
              </div>
              <p>Системы автоматической пожарной сигнализации</p>
            </div>
            <div className = 'item'>
            <div className = 'itemFhoto'>
                <img src={photo3} alt=""/>
              </div>
              <p>Системы оповещения и управления эвакуацией людей при пожаре</p>
            </div>
            <div className = 'item'>
            <div className = 'itemFhoto'>
                <img src={photo4} alt=""/>
              </div>
              <p>Системы оповещения и управления эвакуацией людей при пожаре</p>
            </div>
          </div> 
            : ''}
            </ReactCSSTransitionGroup>

            <button onClick = {this.handleClick} type = 'button' className = 'btnServises'>
              {this.state.isToggleOn ? 'Скрыть': 'Показать ещё'}
            </button>
            

          </div>
          </ReactCSSTransitionGroup>
        </div>
        
      )
    }
  }
  // const [isShow, setShow] = useState(0);
  // const buttonHandler = () => {
  //   setShow(current => !current)
  // }


export default Servises;


