import React from 'react';
import s from './project.module.css';
import Carousel from 'react-elastic-carousel';
import styleCarousel from './carousel.css';
import photo1 from '../../img/1_640.png';
import photo2 from '../../img/2_640.jpg';
import photo3 from '../../img/3_640.jpg';
import photo4 from '../../img/4_640.jpg';
import photo5 from '../../img/5_640.png';

export default function Project({ id }) {

  const state =  [
      { 
        id: photo1 ,
        title: 'Трц “Квадрат-аврора”',
        text: 'Перечень выполненных работ:'
      },
      { 
        id: photo2 ,
        title: 'Мемориал памяти жертвам голодомора в Украине  ', 
        text: 'Перечень выполненных работ:'
      },
      { 
        id: photo3 ,
        title: 'Трц “Квадрат-аврора”',
        text: 'Перечень выполненных работ:'
      },
      { 
        id: photo4 ,
        title: 'Мемориал памяти жертвам голодомора в Украине  ', 
        text: 'Перечень выполненных работ:'
      },
      { 
        id: photo5 ,
        title: 'Трц “Квадрат-аврора”',
        text: 'Перечень выполненных работ:'
      }
    ]
    
    const breakPoints = [
      { width: 1, itemsToShow: 1 },
      { width: 550, itemsToShow: 2, itemsToScroll: 2 },
      { width: 768, itemsToShow: 3 },
      { width: 1200, itemsToShow: 4 }
    ];

  return (
    <div className = {s.project} id={id}>
      <div className = {s.projectTitle}>
        <h2>Ключевые проекты {state.title}</h2> 
        <p className ={s.subTitle}>Познакомьтесь с примерами реализованных нами проектов.</p>
      </div>
      <Carousel 
        breakPoints={breakPoints}  
      >
      {
        state.map( text => 
          <div className = {s.item}>
          <div className = {s.titleItem}>
            <h3>{text.title}</h3>
          </div>
          <div className={s.servBox}>
            <div className = {s.itemText}>  
              <div className = {s.someText}>
                {text.text}
              </div>
            </div>
            <img src={text.id} alt=""/>
          </div>
        </div>  
        )
      }
      </Carousel>
    </div>
  )
}
