import React, { Component } from 'react';
import s from './header.module.css';
import { Link, animateScroll as scroll } from "react-scroll";
import MobileMenu from '../mobileMenu';
// import { fallDown as Menu } from 'react-burger-menu';






export default class Header extends Component  {
    scrollToTop = () => {
        scroll.scrollToTop();
      };
      showSettings (event) {
        event.preventDefault();
        
        
        
      }
    render(){
        return (
            <nav className = {s.header} id="navbar">
                <MobileMenu />
                <ul className = {s.menuMain}>
                    <li className={s.leftItem}>
                        
                    </li>
                    <li className={s.leftItem}>
                    <Link
                        activeClass="active"
                        to="section3"
                        spy={true}
                        smooth={true}
                        offset={-70}
                        duration={500}
                    >Проекты</Link>
                    </li>
                    <li className={s.leftItem}>
                    <Link
                        activeClass="active"
                        to="section1"
                        spy={true}
                        smooth={true}
                        offset={-70}
                        duration={500}
                    >Услуги</Link>
                    </li>
                    <li className={s.rightItem}>
                    <Link
                        activeClass="active"
                        to="section4"
                        spy={true}
                        smooth={true}
                        offset={-70}
                        duration={500}
                    >Контакты</Link>
                    
                    </li>
                    <li className={s.rightItem}>
                    <Link
                        activeClass="active"
                        to="section2"
                        spy={true}
                        smooth={true}
                        offset={-70}
                        duration={500}
                    >O компании </Link>
                    </li>
                </ul>
                    <img
                        src= "https://html5book.ru/wp-content/uploads/2017/04/lily-logo.png"
                        className={s.navBarLogo}
                        alt="Logo."
                        onClick={this.scrollToTop}
                    />
                
            </nav>
        )
    }
    
}