import React from 'react';
import { bool , func} from 'prop-types';
import { StyledMenu } from './Menu.styled';
import { Link, animateScroll as scroll } from "react-scroll";

const Menu = ({ open, setOpen, ...props }) => {
  
  const isHidden = open ? true : false;
  const tabIndex = isHidden ? 0 : -1;

  // const [isToggled, setIsToggled] = React.useState(true);

  return (
     <StyledMenu open={open} aria-hidden={!isHidden} {...props}>
        <Link
          onClick = {() => setOpen(!open)}
          className="menu-item"
          activeClass="active"
          to="section3"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
        >Проекты</Link>
        <Link
          onClick = {() => setOpen(!open)}
          activeClass="active"
          to="section1"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
        >Услуги</Link>
        <Link
          onClick = {() => setOpen(!open)}
          activeClass="active"
          to="section4"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
        >Контакты</Link>
        <Link
          onClick = {() => setOpen(!open)}
          activeClass="active"
          to="section2"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
        >O компании </Link>
    </StyledMenu> 
  )
}

Menu.propTypes = {
  open: bool.isRequired,
  setOpen: func.isRequired,
}

export default Menu;