import React from 'react';
import s from './company.module.css';

export default function Company({ id }) {
  return (
    <div className = {s.company} id={id}>
      <div className = {s.companyTitle}>
        <h2>О компании</h2>
      </div>
      <div className = {s.aboutCompany}>
        <div className = {s.description}>
          <p>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Mauris in augue diam.
            Mauris at luctus purus. Orci varius natoque penatibus et magnis dis parturient montes,
            nascetur ridiculus mus. Morbi quis nulla quis nulla imperdiet accumsan.
            Quisque vehicula lacinia porttitor. Vivamus tempus sed tellus sed.
          </p>
        </div>
        <div className = {s.photoCompany}>
          <img src="" alt=""/>
        </div>
      </div>
    </div>
  )
}
